.App {
  text-align: center;
}

.navbar{
  padding : 8px 100px;
  /* background-color: #f6f5f1; */
}

/* .navbar menu{
  padding: 8px 24px;
} */
.bg{
  background-image: url(./bg3.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100%;
  margin: 0;
  padding: 32px;
}
/* .bg-content{
  display: flex;
  align-items: center;
  justify-content: center;
} */
.bg img{
  width:420px;
  height:420px;
  opacity: 85%;
  border-radius: 3%;
  margin: 50px;
}

.bg Button{
  margin: 24px;
}

.bg h1{
  text-shadow: 30px 35px 0 rgba(0, 0, 0, 0.16);
  font-family: 'Nanum Myeongjo', serif;
  font-weight: bold;
  font-size: 2rem;
  line-height: 1.15;
  text-align: center;
  color: #3d3d3d;
}
.bg p{
  margin-top : 3.125em;
  font-family: 'Nanum Myeongjo', serif;
  font-size: 18px;
  padding-bottom: 36px;
}

.imgB{
  margin-left:400px;
  display:flex;
  width: 40%;
  padding: 3rem;
}

.main-banner{
  margin:200px ;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.content{
  
  display:table;
  position:absolute;
  top:0;
  left:0;
  background-color:rgba(0,0,0,0.5);

  width:100%;
  height:100%;
  /* border:10px solid yellow; */
  box-sizing:border-box;

  opacity:0;
  transition:all 1s;
  cursor:pointer;
}
.content .content-inner{
  font-family: 'Nanum Myeongjo', serif;
  display:table-cell;
  text-align:left;
  vertical-align: center;
  /* border:10px solid blue; */
  color: #fff;

  padding:60px 30px;
}
.content:hover{
  opacity:1;
}

.publish{
  display:flex;
  border-radius: 5%;
  margin: 30px 15%;
  background-color: #F9F5EF;
  /* position:relative; */
}
.publish img{
  width: 30%;
  height: 30%;
  margin: 100px 100px 0 0 ;
  /* position:absolute; */
  /* top:10%;
  right:10%; */
}
.publish .title-input{
  padding : 10px;
  font-size : 24px;
  border-radius: 5%;
  width : 50%;
  border: 1px solid  #F9F5EF;
  margin: 100px 50px 50px 100px;
  background-color: #F9F5EF;

}
.publish .content-input{
  padding : 10px;
  font-size : 16px;
  border-radius: 5%;
  width : 50%;
  height: 500px;
  border: 1px solid  #F9F5EF;
  margin: 50px 50px 50px 100px;
  background-color: #F9F5EF;
}
.publish button{
  margin-top : 10px;
  margin: auto;
}

