/* 애니메이션 시작 때 적용할 css */
.wow-enter{
    opacity: 0;
}
/* 애니메이션 동작 때 적용할 css */
.wow-enter-active{
    opacity: 1;
    /* opacity 서서히 변하도록 */
    transition : all 500ms;
}

.my-alert{
    background : #eeeeee;
    padding : 15px;
    border-radius : 5px;
    max-width : 500px;
    width : 100%;
    margin : auto;
}
